/* ## Title Area
--------------------------------------------- */

.title-area {
	float: left;
	padding-bottom: 25px;
	padding-top: 25px;
}

.wp-custom-logo .title-area {
	max-width: 350px;
	padding-bottom: 5px;
	padding-top: 5px;
	width: 100%;
	img {
		width: auto;
	}
}

.site-title {
	font-size: 20px;
	font-size: 2rem;
	font-weight: 600;
	line-height: 1;
	margin-bottom: 0;
	a {
		color: #333;
		text-decoration: none;
		&:focus, &:hover {
			color: #333;
			text-decoration: none;
		}
	}
}

.site-description, .wp-custom-logo .site-title {
	border: 0;
	clip: rect(0, 0, 0, 0);
	height: 1px;
	overflow: hidden;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}

